import apiFetch from "@wordpress/api-fetch"
import { InnerBlocks, InspectorControls, MediaUpload, MediaUploadCheck } from "@wordpress/block-editor"
import { Button, PanelBody, PanelRow } from "@wordpress/components"
import { registerBlockType } from "@wordpress/blocks"
import { useEffect } from "@wordpress/element"

registerBlockType("blocks/banner", {
    title: "Banner",
    supports: {
        align: ["full"]
    },
    attributes: {
        align: { type: "string", default: "full" },
        imgID: { type: "number" },
        imgURL: { type: "string", default: window.banner.fallbackimage }
    },
    edit: EditComponent,
    save: SaveComponent
})

function EditComponent(props) {

    useEffect(function () {
        if (props.attributes.imgID) {
            async function go() {
                const response = await apiFetch({
                    path: `/wp/v2/media/${props.attributes.imgID}`,
                    method: "Get"
                })
                props.setAttributes({ imgURL: response.media_details.sizes.pageBanner.source_url })
            }
            go()
        }
    }, [props.attributes.imgID])

    function onFileSelect(x) {
        props.setAttributes({ imgID: x.id })
    }

    return (
        <>
            <InspectorControls>
                <PanelBody title="background" initialOpen={true}>
                    <PanelRow>
                        <MediaUploadCheck>
                            <MediaUpload
                                onSelect={onFileSelect}
                                value={props.attributes.imgID}
                                render={({ open }) => {
                                    return <Button onClick={open}>Choose Image</Button>
                                }} />
                        </MediaUploadCheck>
                    </PanelRow>
                </PanelBody>
            </InspectorControls>
            <div className="banner" style={{ backgroundImage: `url('${props.attributes.imgURL}')` }}>
                <InnerBlocks allowedBlocks={['blocks/genericheading', 'blocks/genericbutton']} />
            </div>
        </>

    )
}

function SaveComponent() {
    return <InnerBlocks.Content />
}